document.addEventListener("DOMContentLoaded", function () {
    iniciarApp();
});

// creando mi funcion aqui abajo aligeramos (y damos mas orden) la parte de arriba
function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija() {
    const barra = document.querySelector(".header");
    const sobreFestival = document.querySelector(".sobre-festival");
    const body = document.querySelector("body");
    window.addEventListener("scroll", function () {
        if (sobreFestival.getBoundingClientRect().bottom < 0) {
            barra.classList.add("fijo");
            body.classList.add("body-scroll"); // para agregarle el  padding-top que reemplaza el hueco que deja mi barra al hacerse fixed
        } else {
            barra.classList.remove("fijo");
            body.classList.remove("body-scroll");
        }
    });
}

function scrollNav() {
    const enlaces = document.querySelectorAll(".navegacion-principal a");
    // como usamos querySelectorAll entonces debemos usar un forEach junto al addEventListener para iterar en cada uno de ellos (no se puede usar el addEventListener solito)

    enlaces.forEach((enlace) => {
        // console.log(enlace); // el argumento enlace es cada uno de los enlaces
        enlace.addEventListener("click", (e) => {
            // console.log(e.target); // al que le doy click
            e.preventDefault(); // previene la accion por defecto de llevarme a la seccion de manera brusca y directa. debemos usar esto si usaremos nuestro smooth
            const seccionScroll = e.target.attributes.href.value;
            const seccion = document.querySelector(seccionScroll);
            // scrollIntoView es una funcion propia de js
            seccion.scrollIntoView({ behavior: "smooth" });
        });
    });
}

// como seran diferentes funciones las registro en el addEventListener y las mando a llamar mediante otra funcion
function crearGaleria() {
    const galeria = document.querySelector(".galeria-imagenes");
    for (let i = 1; i <= 12; i++) {
        const imagen = document.createElement("picture");
        imagen.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif"/>
            <source srcset="build/img/thumb/${i}.webp" type="image/webp"/>
            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen galeria"/>
        `;
        imagen.onclick = function () {
            // tengo que colocarlo como callback pq si lo hago de la otra manera se muestra todo, tipo llamando la funcion
            mostrarImagen(i);
        };

        galeria.appendChild(imagen);
    }
    // galeria.textContent = "vamos a crear la galeria";
}

function mostrarImagen(id) {
    const imagen = document.createElement("picture");
    imagen.innerHTML = `
        <source srcset="build/img/grande/${id}.avif" type="image/avif"/>
        <source srcset="build/img/grande/${id}.webp" type="image/webp"/>
        <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen galeria"/>
    `;
    // Crea el Overlay con la imagen
    const overlay = document.createElement("div");
    overlay.classList.add("overlay");
    overlay.appendChild(imagen);

    // Boton para cerrar el modal
    const cerrarModal = document.createElement("p");
    cerrarModal.textContent = "X";
    cerrarModal.classList.add("btn-cerrar");
    cerrarModal.onclick = function () {
        const body = document.querySelector("body"); // * por el tema de scope ayuda (creo que no interfiere con el const body creado mas abajo)
        body.classList.remove("fijar-body");
        overlay.remove();
    };
    overlay.onclick = function () {
        const body = document.querySelector("body"); // * por el tema de scope ayuda (creo que no interfiere con el const body creado mas abajo)
        body.classList.remove("fijar-body");
        overlay.remove();
    };
    overlay.appendChild(cerrarModal);

    // AÃ±adirlo al HTML
    const body = document.querySelector("body"); // aun asi sea un bodu o a o los demas, deben ir entre comillas
    body.appendChild(overlay);
    body.classList.add("fijar-body"); // para fijar mi body
}
